.homepage {
  display:flex;
  width:800px;
  flex-direction:column;
  justify-content: space-around;
  align-items: center;
  padding:5px;
  background:lightgrey;
}

.homepage>* {
  margin-bottom:10px;
}

.auth-img {
  width:400px;
  height:400px;
}

.author-of-the-day {
  text-align: center;
}

.autor-of-the-day {
  width: 80%;
  height: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: rgb(207, 204, 204);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.autor-of-the-day a {
  display: flex;  
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}


.autor-of-the-day img {
  width: 400px;
  height: 500px;
  margin: 10px 0;
} 

.autor-of-the-day span {
  font-size: 35px;
  font-weight: bold;
  color: black;
}

.autor-of-the-day span:hover {
  color: rgb(82, 45, 45);
}
.developer {
  margin: 10px 20px;
  padding: 10px;
  background-color: rgb(143, 140, 140);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.developer a {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 19px;
  color: rgb(0, 0, 0);
  text-decoration: none;
}

.developer img {
  margin-bottom: 10px;
}

.developer a:hover {
  color: rgb(243, 243, 243);
}
.about-us {
  width: 80%;
  padding: 10px;  
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(209, 207, 207);
}

.our-developers {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}


.author-image {
  grid-area: image;
}

.list-of-works{
  grid-area:  works;
}

.timeline {
  grid-area: timeline;
}

.photo-list {
  grid-area: photos;
}

.video-list {
  grid-area: video;
}

.map-component {
  grid-area: map;
}

.author-page {
  display:grid;
  grid-template-columns: 100px 100px;
  grid-template-rows: auto;
  grid-gap:10px;
  grid-template-areas: 
    "image timeline"
    "works timeline"
    "photos photos"
    "video video"
    "map map"
  ;
  justify-items: center;
  align-items: center;
}

button.active {
  background-color: aquamarine;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  font-size: 16px;
  font-family: sans-serif, serif;
}

#root {
  width: 100%;
  height: 100%;
}

.main {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  width: 50%;
  display: flex;
  justify-content: space-between;
}

.content {
  width: 100%;
  padding: 15px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h2 {
  font-size: 30px;
}

.lang {
  position:absolute;
  top:0;
  right:50px;
}


